.item {
    position: relative;
    width: 150px;
    border: 1px solid grey;
    background-color: aqua;
    border-radius: 5px;
  
    padding: 10px;
  }
  
  .item:not(:last-child) {
    margin-right: 5px;
  }
  .status {
    position: absolute;
    top: 5px;
    left: 5px;
  
    width: 15px;
    height: 15px;
  
    border-radius: 50%;
  
    background-color: green;
  }
  
  .avatar {
    display: inline-flex;
    justify-content: center;
    width: 80px;
    background-color: aqua;
  }
  
  .name {
    margin-bottom: 0;
    font-size: 18px;
    font-weight: 700;
  }
  
  .offline {
    composes: status;
    background-color: red;
  }